import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { OrgRequestParam, SaveGroupReqeustParam } from './index.type';
import { of } from 'rxjs';

@Injectable()
export class ApiService {

  absUrl: string;

  groupApiUrl: string;
  
  getOrgsUrl: string;

  constructor(
    private http: HttpClient
  ) { }

  /**
    * 
    * @returns observable
    */
  getOrganization(param: OrgRequestParam) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.post(this.absUrl + '/org', param, options);
  }

  /**
     * 
     * @returns observable
     */
   getOrganizations(param: any) {
    const options = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json',
        }),
    };
    let url = this.getOrgsUrl + `/getorgs?withDisabledOrg=${param.withDisabledOrg}&gettingCurrentOrg=${param.gettingCurrentOrg}&onlyDisplayOrg=${param.onlyDisplayOrg}`;
    return this.http.post(url, options);
}

/**
 * 
 * @returns observable
 */
getOrganizationsWithLayer(param: any) {
    const options = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json',
        }),
    };
    let url = this.getOrgsUrl + `/getOrgsWithLayer?withDisabledOrg=${param.withDisabledOrg}`;
    if(param.parentOrgId) {
      url = url + `&parentOrgId=${param.parentOrgId}`
    }
    url = url + `&gettingCurrentOrg=${param.gettingCurrentOrg}&onlyDisplayOrg=${param.onlyDisplayOrg}`;
    return this.http.post(url, options);
}

/**
 * id
 * @returns observable
 */
 getOrganizationsWithIds(ids: string[]) {
  const options = {
      headers: new HttpHeaders({
          'Content-Type': 'application/json',
      }),
  };
  return this.http.post(this.getOrgsUrl + '/getorgsbyids', JSON.stringify(ids), options);
}

/**
 * 
 * @returns observable
 */
getOrganizationsWithFilter(queryFilter: any) {
    const options = {
        headers: new HttpHeaders({
            'Content-Type': 'application/json',
        }),
    };
    return this.http.post(this.getOrgsUrl + '/query', JSON.stringify(queryFilter), options);
}

  getOrgGroups(absOrgType: string) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.get(this.groupApiUrl + '/' + absOrgType, options);
  }

  getGroupMembers(groupId: string, absOrgType: string) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    if (!groupId) {
      return of(null);
    }
    const url = this.groupApiUrl + '/' + groupId + '/absOrgType/' + absOrgType;
    return this.http.get(url, options);
  }

  saveGroup(groupInfo: SaveGroupReqeustParam) {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
      }),
    };
    return this.http.post(this.groupApiUrl, groupInfo, options);
  }
}
